import React from 'react';
import {Link} from 'react-router';
import {PageHeader, Panel, Table, Button, Badge, Glyphicon, Pagination} from 'react-bootstrap';

const HomePage = () => {
  return (
    <div>
      <h3>
        Manage All Blogs here
        <small>&nbsp; Help: Contact admin@admin.com</small>
      </h3>
      <p>This tool will help you manage all your blog posts hosted at Nation Builder</p>
      <Panel header={<span> <Glyphicon glyph="th-large" /> All Blogs</span>} bsStyle="success">
        <Table striped condensed hover>
          <thead>
          <tr>
            <th>#</th>
            <th>Blog Details</th>
            <th>Action</th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>1</td>
            <td>
              Will Wars - Foobar Softwares
              <p>Tags <Badge>Scientific</Badge> <Badge>Discovery</Badge></p>
            </td>
            <td>
              <Button bsStyle="danger"><Glyphicon glyph="remove"/> Delete</Button>
            </td>
          </tr>
          <tr>
            <td>2</td>
            <td>
              <p>Perspective Misalignments - Foobar Softwares</p>
              <p>Tags <Badge>Funny</Badge> <Badge>Humor</Badge></p>
            </td>
            <td>
              <Button bsStyle="danger"><Glyphicon glyph="remove"/> Delete</Button>
            </td>
          </tr>
          </tbody>
        </Table>


      </Panel>
      <div className="text-center">
        <Pagination
          bsSize="medium"
          items={4}/>
      </div>

    </div>
  );
};

export default HomePage;



